const className = 'image-grid--v1';

function launchImgGrid(imgGrid) {
    let els = imgGrid.querySelectorAll('.image-grid--v1 img');
    window.addEventListener('scroll', fadeIn);
    function fadeIn() {
        for (let i = 0; i < els.length; i++) {
            let distInView =
                imgGrid.getBoundingClientRect().top - window.innerHeight + 20;
            if (distInView < 0) {
                els[i].classList.add('inView');
            }
        }
    }
    fadeIn();
}

export default {
    launchFn: launchImgGrid,
    launchQuery: `.${className}`,
};
